// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 16) * 1px;
	font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

@mixin picture-text {
	.entry-header {
		letter-spacing: .2rem;
		position: relative;
		line-height: 2.5rem;

		img {
			width: 100%;
			height: auto;
		}

		.entry-title{
			position: absolute;
			padding: 20px;
			background-color: #248A83;
			top: 0px;
			width: 75%;
			color: white;
			font-size: 32px !important; 
			


			a {
				color: white;
				font-size: 36px;
				text-decoration: none;
				text-transform: uppercase;
			}
		}

		.entry-meta {
			font-size: 14px;
			position: absolute;
			right: 0;
			bottom: 0;
			margin: 14px 0;
			background-color: #248A83;
			color: white;
		}
	} 
}
